.logo-wrapper{
  display: flex;
  font-size: 15px;
  gap: 25px;
  padding: 20px 10px;
  align-content: center;
  align-items: center;
  border: 1px solid black;
}
.opt-ans button{
   margin: 20px 0 0 0;
   padding: 10px 15px;

   
}
.predict-page h1{
  margin: 0 0 0 3rem;
}

.opt-ans{
  display: flex;
  gap:1rem;
  padding: 1rem;
}
.btnARQ, .btnGRA{
  display: block;
  width: 240px;
  font-size: 10px;
  padding: 0.2rem 0;
  margin-top: 5px;

}
.btnAA{
  display: block;
  width: 240px;
  font-size: 20px;
  padding: 0.3rem 0;
}
.btn-ans{
  padding: 2rem 3rem;

}
.predict-list{
  list-style:decimal;
  margin: 0 0 0 3rem;
}
.predict-list li{
  padding: 0.2rem 0;
  border-bottom: 1px solid;
  max-width: 200px;
}

.predict-list li.active {
  color: #fff;
}
